export default function percentCounter(
  itemSelector,
  classProgress,
  subclassPercent
) {
  const items = document.querySelectorAll(itemSelector);
  items.forEach((item) => {
    let progress = item.querySelector(classProgress);
    let percent = item.querySelector(subclassPercent);
    progress.style.setProperty("--percent", percent.textContent);
  });
}
