import { toggleActive } from "./modules/Active";
import percentCounter from "./modules/percentCounter";
import eventListeners from "./modules/eventListeners";
import animateBoot from "./modules/scrollReveals";
import { tns } from "../../node_modules/tiny-slider/src/tiny-slider";
class Handler {
  static control = false;
  static userControl = false;
}
window.addEventListener("load", () => {
  let loadPage = document.querySelector(".load");
  loadPage.classList.add("load__animate");
  setTimeout(() => {
    loadPage.style.display = "none";
    loadPage.classList.remove("load__animate");
    setTimeout(
      () =>
        (document.querySelector("html").style.cssText = "overflow-y:visible;"),
      200
    );
  }, 1900);
});
document.addEventListener("DOMContentLoaded", () => {
  var slider = tns({
    container: ".my-slider",
    nav: false,
    prevButton: ".left-arrow",
    nextButton: ".right-arrow",
    items: 3,
    controls: true,
    responsive: {
      320: {
        gutter: 30,
        items: 1,
        controls: false,
      },
      1000: {
        gutter: 30,
        items: 2,
        controls: true,
      },
      1620: {
        items: 3,
        controls: true,
      },
    },
  });
  document.querySelector(".start__hamburger").addEventListener("click", () => {
    toggleActive(".menu");
  });
  document.querySelector(".menu__close").addEventListener("click", () => {
    toggleActive(".menu");
  });
  percentCounter(".skils__grid-item", ".skils__progress", ".fw500");
  eventListeners();
  animateBoot();
  let socket = new WebSocket("ws://localhost:5555/");
  socket.addEventListener("open", (e) => {
    let icons = document.querySelector(".emoji");
    icons.classList.add("emoji_active");
  });
  socket.addEventListener("error", (e) => {
    console.log(e);
  });
  socket.addEventListener("message", async (e) => {
    if (!Handler.control) {
      Handler.control = true;
      const div = document.createElement("img");
      div.classList.add("gif");
      div.src = `./png/${e.data}.gif`;
      let offset;
      switch (e.data) {
        case "like": {
          offset = 20;
          break;
        }
        case "hello": {
          offset = 170;
          break;
        }
        case "fire": {
          offset = 98;
          break;
        }
        case "kick": {
          offset = 245;
          break;
        }
      }
      div.style.cssText = `left:${offset}px;`;
      const container = document.querySelector(".emoji");
      container.append(div);
      div.classList.add("gif_active");
      div.classList.add("animate-smooth");
      setTimeout(() => {
        div.classList.remove("animate-smooth");
        div.classList.add("animate-reverse-smooth");
        setTimeout(() => {
          div.classList.remove("gif_active");
          Handler.control = false;
          div.remove();
        }, 150);
      }, 2000);
    }
  });
  let icons = document.querySelectorAll(".emoji__icon");
  icons.forEach((item) => {
    item.addEventListener("click", (e) => {
      let message;
      if (item.src.indexOf("like") != -1) {
        message = "like";
      } else if (item.src.indexOf("fire") != -1) {
        message = "fire";
      } else if (item.src.indexOf("hello") != -1) {
        message = "hello";
      } else if (item.src.indexOf("kick") != -1) {
        message = "kick";
      }
      if (!Handler.userControl) {
        Handler.userControl = true;
        setTimeout(() => (Handler.userControl = false), 4000);

        console.log(message);
        socket.send(message);
      }
    });
  });
});
