import { toggleActive } from "./Active";
export default async function message() {
  const from = document.querySelector(".contact__form");
  const form = new FormData(from);

  const response = await fetch("http://localhost:5555/?destination=message", {
    method: "POST",
    body: JSON.stringify(Object.fromEntries(form.entries())),
    headers: {
      "Content-type": "application/json",
    },
  });
  const result = await response.text();
  from.reset();
  return result;
}
