import ScrollReveal from "scrollreveal";
export default function animateBoot() {
  ScrollReveal({
    reset: false,
    delay: 500,
    duration: 2500,
    distance: "10px",
  });
  ScrollReveal().reveal(".about-me__image", { origin: "left" });
  ScrollReveal().reveal(".about-me__general", { origin: "top" });
  ScrollReveal().reveal(".about-me__gitem", {
    origin: "bottom",
    interval: 200,
  });
  ScrollReveal().reveal(document.querySelectorAll(".gallery"), {
    origin: "top",
  });
  ScrollReveal().reveal(document.querySelectorAll(".resume__column")[0], {
    origin: "left",
  });
  ScrollReveal().reveal(document.querySelectorAll(".resume__column")[1], {
    origin: "right",
  });
  ScrollReveal().reveal(document.querySelectorAll(".container >.title"), {
    origin: "top",
  });
  ScrollReveal().reveal(document.querySelectorAll(".separator"), {
    origin: "left",
  });
  ScrollReveal().reveal(document.querySelectorAll(".skils__item"), {
    origin: "top",
    interval: 150,
  });
  ScrollReveal().reveal(document.querySelector(".start__link"), {
    origin: "left",
  });
  ScrollReveal().reveal(document.querySelectorAll(".start__link")[1], {
    origin: "right",
  });
  ScrollReveal().reveal(document.querySelector(".contact__wrapper"), {
    origin: "left",
  });
  ScrollReveal().reveal(document.querySelector(".contact__text"), {
    origin: "right",
  });
  ScrollReveal().reveal(document.querySelectorAll(".skils__grid-item"), {
    origin: "bottom",
    interval: 200,
  });
  ScrollReveal().reveal(document.querySelectorAll(".price__grid-item"), {
    origin: "bottom",
    distance: "0px",
    interval: 200,
  });
  const worksGridItem = document.querySelectorAll(".works__grid-item");
  const [first, second, third] = worksGridItem;
  ScrollReveal().reveal([first, second, third], {
    origin: "top",
    interval: 300,
  });
  const array = [];
  worksGridItem.forEach((item, i) => {
    if (i > 3) {
      array.push(item);
    }
  });
  ScrollReveal().reveal(worksGridItem[3], {
    origin: "right",
    distance: "0px",
  });
  ScrollReveal().reveal(array, {
    origin: "bottom",
    interval: 200,
  });
}
ScrollReveal().reveal(document.querySelector(".socials").childNodes, {
  origin: "top",
  interval: 200,
  distance: "200px",
  delay: 600,
});
