export function toggleActive(classSelector) {
  const elem = document.querySelector(classSelector);
  if (classSelector.indexOf("menu") !== -1) {
    console.log(classSelector);
    if (elem.classList.contains((classSelector + "_active").replace(/./, ""))) {
      document.documentElement.style.overflow = "visible";
    } else {
      document.documentElement.style.overflow = "hidden";
    }
  }
  elem.classList.toggle((classSelector + "_active").replace(/./, ""));
}
