import { toggleActive } from "./Active";
import message from "./message";

export default function eventListeners() {
  const inputAuth = document.querySelectorAll(".menu__auth input");
  inputAuth.forEach((item) => {
    item.addEventListener("focus", (e) => {
      item.nextElementSibling.classList.add("active");
    });
    item.addEventListener("blur", (e) => {
      if (!item.value) {
        item.nextElementSibling.classList.remove("active");
      }
    });
  });
  document.querySelector(".menu__close-auth").addEventListener("click", () => {
    document.querySelector(".menu__auth").classList.remove("menu__auth_active");
    menuButton.style.display = "block";
    setTimeout(() => {
      document.querySelector(".menu__auth").style.display = "none";
    }, 500);
  });
  const menuButton = document.querySelector(".menu__button");
  /*  menuButton.addEventListener("click", () => {
    menuButton.style.display = "none";
    const modalAuth = document.querySelector(".menu__auth");
    if (modalAuth.classList.contains("menu__auth_active")) {
      modalAuth.classList.remove("menu__auth_active");
      setTimeout(() => {
        modalAuth.style.display = "none";
      }, 500);
    } else {
      if (window.innerWidth <= 768) {
        modalAuth.style.boxShadow =
          "0 4px 6px rgba(0, 0, 0, 0.6), 0 0 12px rgba(255, 0, 0, 0.6)";
      }
      modalAuth.style.display = "flex";
      setTimeout(() => {
        modalAuth.classList.add("menu__auth_active");
      }, 200);
    }
  }); */
  const contactWrapperImg = document.querySelector(".contact__wrapper img");
  const contactWrapper = document.querySelector(".contact__wrapper");
  contactWrapperImg.addEventListener("mouseenter", () => {
    contactWrapper.style.setProperty("--visible", "flex");
    setTimeout(() => {
      toggleActive(".contact__wrapper");
    }, 0);
  });
  contactWrapper.addEventListener("mouseleave", () => {
    toggleActive(".contact__wrapper");
    setTimeout(() => {
      contactWrapper.style.setProperty("--visible", "none");
    }, 500);
  });
  document
    .querySelector(".contact__form")
    .addEventListener("submit", async (E) => {
      E.preventDefault();
      const menu = document.querySelector(".menu_black");
      menu.style.display = "flex";
      document.documentElement.style.overflow = "hidden";
      document.querySelector(".menu__list").style.display = "none";
      setTimeout(() => {
        menu.classList.toggle("menu_black_active");
      });
      const result = await message();
      menu.classList.toggle("menu_black_active");
      setTimeout(() => {
        menu.style.display = "none";
      }, 1450);
      document.documentElement.style.overflow = "";
      document.querySelector(".menu__list").style.display = "flex";
      document.querySelector(".notification").style.display = "flex";
      setTimeout(() => {
        if (result.indexOf("good") != -1) {
          document
            .querySelector(".notification")
            .classList.add("notification_active-good");
          setTimeout(() => {
            document
              .querySelector(".notification")
              .classList.remove("notification_active-good");
            setTimeout(() => {
              document.querySelector(".notification").style.display = "none";
            }, 500);
          }, 2000);
        } else {
          document
            .querySelector(".notification")
            .classList.add("notification_active-poor");
          setTimeout(() => {
            document
              .querySelector(".notification")
              .classList.remove("notification_active-poor");
            setTimeout(() => {
              document.querySelector(".notification").style.display = "none";
            }, 500);
          }, 2000);
        }
      });
    });
}
